import alt from '../utils/Alt';
import ChatActions from '../actions/ChatActions';
import SurveySource from '../sources/SurveySource';
import SurveyActions from '../actions/SurveyActions';
import {ResponseType} from '../constants/Enums';

class ChatStore {
  constructor() {
    this.chatLog = [];
    this.botThinking = false;
    this.currentChatLogPosition = 0;
    this.chatLogID = 0;
    this.surveyIsReady = false;
    this.survey = [];
    this.errorMessage = null;
    this.responseType = ResponseType.None;
    this.images = {
      user: 'placeholder',
      bot: 'placeholder'
    };
    this.submitSurvey = {
      disableSubmitButton: false,
      message: 'Submit'
    };
    this.bindListeners({
      handlePushUserResponse: ChatActions.PUSH_USER_RESPONSE,
      handlePushBotResponse: ChatActions.PUSH_BOT_RESPONSE,
      handleFetchSurveyInProgress: SurveyActions.FETCH_SURVEY_IN_PROGRESS,
      handleFetchSurveySuccess: SurveyActions.FETCH_SURVEY_SUCCESS,
      handleFetchingSurveyFailed: SurveyActions.FETCH_SURVEY_FAILED,
      handleSubmitSurveyInProgress: SurveyActions.SUBMIT_SURVEY_IN_PROGRESS,
      handleSubmitSurveyFailed: SurveyActions.SUBMIT_SURVEY_FAILED,
      handleSubmitSurveySuccess: SurveyActions.SUBMIT_SURVEY_SUCCESS
    });
    this.registerAsync(SurveySource);
  }

  handlePushUserResponse(message) {
    const chatBotDelay = (Math.random()+0.5)*1000;
    this.survey[this.currentChatLogPosition].response.content = message;
    this.currentChatLogPosition += 1;
    this.chatLog.push({id: this.chatLogID, message: message, user: 'user'});
    this.chatLogID += 1;
    this.botThinking = true;
    setTimeout(() => {
      ChatActions.pushBotResponse();
    }, chatBotDelay);
  }

  handlePushBotResponse() {
    this.botThinking = false;
    this.chatLog.push({id: this.chatLogID, message: this.survey[this.currentChatLogPosition].call.content, user: 'bot'});
    this.chatLogID += 1;
  }

  handleFetchSurveyInProgress() {
    this.survey = [];
  }

  handleFetchSurveySuccess(survey) {
    this.survey = survey;
    this.errorMessage = null;
    this.surveyIsReady = true;
  }

  handleFetchingSurveyFailed(errorMessage) {
    this.errorMessage = errorMessage;
  }

  handleSubmitSurveyInProgress() {
  }

  handleSubmitSurveyFailed(errorMessage) {
  }

  handleSubmitSurveySuccess() {
  }
}

export default alt.createStore(ChatStore, 'ChatStore');



