import React from 'react';
export default class HourSelection extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }
  render() {
    return(
      <div className='UserResponseHoursSlept'>
        <button onClick={this.handleClick.bind(this, 'More than 8')}>More than 8 hours</button>
        <button onClick={this.handleClick.bind(this, '6-8 hours')}>6-8 hours</button>
        <button onClick={this.handleClick.bind(this, '4-6 hours')}>4-6 hours</button>
        <button onClick={this.handleClick.bind(this, '2-6 hours')}>2-6 hours</button>
        <button onClick={this.handleClick.bind(this, '0-2 hours')}>Less than 2 hours</button>
      </div>
    );
  }

  handleClick(event) {
    this.props.ChatActions.pushUserResponse(event);
    this.props.submitSurvey(this.props.ChatStore.currentChatLogPosition);
  }
}



