export default [
  {
    id: 0,
    call: {
      type: 'question',
      content: `Good morning! You\'ve started at ${time.getTime()}.\nHow are you feeling today?`
    },
    response: {
      type: ResponseType.FaceMood
    }
  },
  {
    id: 1,
    call: {
      type: 'statement',
      content: 'Thanks for letting me know! Did you get much sleep last night?\nAround how many hours?'
    },
    response: {
      type: ResponseType.HoursSlept
    }
  },
  {
    id: 2,
    call: {
      type: 'question',
      content: 'Do you have any other comments you would like to make?'
    },
    response: {
      type: ResponseType.TextEntry
    }
  }, {
    id: 3,
    call: {
      type: 'question',
      content: 'Thanks for letting me know! I will see you next time!'
    },
    response: {
      type: ResponseType.None
    }
  }
];



