import React from 'react';
export default class UserResponseFaceMood extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(faceType) {
    this.props.ChatActions.pushUserResponse(faceType);
    this.props.submitSurvey(this.props.ChatStore.currentChatLogPosition);
  }

  render() {
    return(
      <div className='UserResponseFaceMood'>
        <button className= 'UserResponseFaceMoodChildren' onClick={this.handleClick.bind(this, 'happy')}>Happy</button>
        <button className= 'UserResponseFaceMoodChildren' onClick={this.handleClick.bind(this, 'meh')}>Meh</button>
        <button className= 'UserResponseFaceMoodChildren' onClick={this.handleClick.bind(this, 'sad')}>Sad</button>
      </div>
    );
  }
}



