import alt from '../utils/Alt';

class ChatActions {
  nextQuestion() {
    this.dispatch();
  }

  pushMessage(message) {
    this.dispatch(message);
  }

  pushUserResponse(message) {
    this.dispatch(message);
  }

  pushBotResponse() {
    this.dispatch();
  }

  botContinueChat() {
    this.dispatch();
  }
}

export default alt.createActions(ChatActions);



