import React from 'react';
import request from 'superagent';
import ErrorMessage from './ErrorMessage';
import LoadingMessage from './LoadingMessage';
import Avatar from './Avatar';

export default class Chatbox extends React.Component {
  constructor(props) {
    super(props);
    this.convertMessage = this.convertMessage.bind(this);
  }

  componentDidUpdate() {
    // TODO: Scroll to latest message
  }

  convertMessage(message) {
    // TODO: Refactor this converter
    switch (message) {
      case 'happy':
        return <img src='/assets/face-happy.svg' />
      case 'meh':
        return <img src='/assets/face-meh.svg' />
      case 'sad':
        return <img src='/assets/face-sad.svg' />
      default:
        return message;
    }
  }

  render() {
    const chatLog = this.props.ChatStore.chatLog;
    const botThinking = this.props.ChatStore.botThinking;
    if (this.props.ChatStore.errorMessage) {
      return <ErrorMessage/>;
    } else if (this.props.ChatStore.survey.length === 0) {
      return <LoadingMessage/>;
    } else {
      return(
        <div className='ChatBox'>
          <div className='messages-wrapper'>
            {
              chatLog.map((chatLog) => {
                if (chatLog.user === 'user') {
                  return (
                    <div key={chatLog.id} className='MessageContainer'>
                      <div className='LeftChatMessage'>{this.convertMessage(chatLog.message)}</div>
                      <Avatar position='RightAvatar' user={chatLog.user} url={this.props.ChatStore.images.user}/>
                    </div>
                  );
                } else if (chatLog.user === 'bot') {
                  return (
                    <div key={chatLog.id} className='MessageContainer'>
                      <Avatar position='LeftAvatar' user={chatLog.user} url={this.props.ChatStore.images.bot}/>
                      <div className='RightChatMessage'>{chatLog.message}</div>
                    </div>
                  );
                }
              })
            }
            {botThinking ?
              <div className='MessageContainer thinking'>
                <div className='RightChatMessage'>...</div>
              </div> : ''}
          </div>
        </div>
      );
    }
  }
}



