export default class DateFormatter {
  constructor() {
    this.datetime = new Date();
  }

  getDate() {
    const date = this.datetime.getDays() + '/' + this.datetime.getMonths() + '/' + this.datetime.getYears();
    return date;
  }

  getTime() {
    let time = '';
    if (this.datetime.getHours() > 11) {
      time = this.datetime.getHours() - 12 + ':' + this.datetime.getMinutes() + ' pm';
    } else {
      time = this.datetime.getHours() + ':' + this.datetime.getMinutes() + ' am';
    }
    return time;
  }
}



